.ecosystem {
	&-wrapper {
		width: 750px;
		margin: 0 auto;
		padding-top: 89px;
		position: relative;
		margin-bottom: 146px;

		@media(max-width: 991px) {
			width: 100%;
		}
	}

	&-image {
		img {
			width: 100%;
		}
	}

	&-item {
		&--left {
			margin-bottom: 45px;

			@media(max-width: 575px) {
				background-color: #fff;
				color: $black;
				max-width: 260px;
				padding: 10px;
				height: 230px;
			}

			.title-md {
				@media(max-width: 575px) {
					color: $yellow;
				}
			}
		}

		&--right {
			margin-bottom: 30px;
		}
	}

	&-list {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;

		.list-item {
			position: absolute;
			font-size: 14px;
			line-height: 1.7;

			&--1 {
				top: 35px;
				left: 50%;
				transform: translateX(-50%);
			}

			&--2 {
				right: 102px;
				top: 91px;

				@media(max-width: 991px) {
					right: 93px;
				}
			}

			&--3 {
				right: 0;
				top: 247px;

				@media(max-width: 991px) {
					top: 233px;
				}
			}

			&--4 {
				right: 0;
				bottom: 147px;

				@media(max-width: 991px) {
					bottom: 133px;
				}
			}

			&--5 {
				right: 102px;
				bottom: -10px;

				@media(max-width: 991px) {
					right: 93px;
				}
			}

			&--6 {
				bottom: -58px;
				left: 50%;
				transform: translateX(-50%);
			}

			&--7 {
				left: 102px;
				bottom: -10px;

				@media(max-width: 991px) {
					left: 93px;
				}
			}

			&--8 {
				left: 0;
				bottom: 147px;

				@media(max-width: 991px) {
					bottom: 133px;
				}
			}

			&--9 {
				left: 0;
				top: 247px;

				@media(max-width: 991px) {
					top: 233px;
				}
			}

			&--10 {
				left: 102px;
				top: 91px;

				@media(max-width: 991px) {
					left: 93px;
				}
			}
		}
	}
}