.about {
	&-panel {
		&--mobile {
			@media(max-width: 575px) {
				margin-top: -35px;
			}
		}

		&--left {
			width: 300px;
			height: 263px;
			background: #ffffff;
			box-shadow: 0 0 7px rgba(0, 0, 0, 0.3);
			position: relative;
			left: 114px;
			top: -120px;
			z-index: 2;
			color: #212121;
			line-height: 1.17;
			padding-top: 35px;
			padding-left: 30px;

			@media(max-width: 991px) {
				width: 100%;
				height: auto;
				top: initial;
				left: initial;
				box-shadow: none;
			}
		}

		&--right {
			position: relative;
			z-index: 1;
			width: 500px;
			left: 43px;
			text-align: justify;

			@media(max-width: 991px) {
				width: 100%;
				left: initial;
			}
		}

		&--hr {
			&:before {
				content: '';
				display: block;
				width: 773px;
				height: 5px;
				background: #fda700;
				position: absolute;
				bottom: -5px;
				right: 0;

				@media(max-width: 991px) {
					width: 100%;
				}
			}
		}

		&--parts-dotted {
			&:before {
				content: '';
				display: block;
				width: 187px;
				height: 195px;
				position: absolute;
				bottom: -120px;
				right: -115px;
				background: url('../img/parts-dotted-5.png') no-repeat 50% 50% / 187px;
				z-index: -1;
			}
		}

		.path--top {
			background: #f6f6f6;
			color: #212121;
			padding: 33px 68px 27px 60px;
			line-height: 1.6;
			letter-spacing: -.1px;

			@media(max-width: 575px) {
				padding: 33px 20px 27px 20px;
			}
		}

		.path--bottom {
			background: #212121;
			color: #ffffff;
			padding: 25px 70px 24px 60px;
			line-height: 1.6;

			@media(max-width: 575px) {
				padding: 25px 20px 24px 20px;
			}
		}
	}
}