.team__member {
    text-align: center;
    padding: 35px 15px;
    background-color: #f6f6f6;
    margin: 20px 0;
}

.member__image {
    min-height: 210px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.member__socials {
    margin: 20px 0;
    a {
        fill: #6e6e6e;
        transition: .3s;
        &:hover {
            fill: $yellow;
        }
    }
}

.member__name {
    font-weight: 600;
    text-transform: uppercase;
    font-size: 16px;
    margin: 10px 0;
}

.member__position {
    font-style: italic;
    font-size: 14px;
}

.team__link {
    color: $yellow;
    cursor: pointer;
}

.team__link-inner {
    color: $yellow;
    cursor: pointer;
}

.member__hidden-desc {
    display: none;
}

.team__main-page {
    margin-top: 95px;
}