.about-panel {
    &--macbook {
        display: inline-block;
        width: 318px;
        margin-right: 42px;
        margin-bottom: 54px;
        position: relative;
        z-index: 1;

        @media(max-width: 575px) {
            width: 290px;
        }

        &-img {
            @media(max-width: 575px) {
                max-width: 100%;
            }
        }
    }

    &--lupa {}

    &--link {
        font-weight: 100;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        // height: 135px;
        // z-index: 2;
        left: 50%;
        bottom: -40px;
        // top: 50%;
        transform: translate(-50%);
        // margin-top: 20px;
        // padding-top: 35px;
        color: $yellow;

        img {
            width: 25px;
        }
    }
}