body {
	font-family: 'PT Sans';
	background: #ffffff;
	color: #ffffff;
}

button,
a {
	outline: none;

	&:focus {
		outline: none;
	}
}

.wrapper {
	overflow: hidden;
	position: relative;

	&--profile {
		background-color: #f7f7f7;
	}
}

.swiper-slide {
	display: flex;
	justify-content: center;
}

.swiper__button {
	position: absolute;
	top: 42%;
	// transform: translateY(-50%);
	z-index: 10;

	&--prev {
		left: -2px;
	}

	&--next {
		right: -2px;
	}

	.fa {
		font-size: 20px;
		font-weight: 300;
		color: #fff;

		&:hover {
			color: $yellow;
		}
	}
}