.testnet__items {
    margin: 65px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    &--start {
        justify-content: flex-start;
        .testnet__item {
            margin-right: 30px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    @media(max-width: 575px) {
        margin: 20px 0;
    }
}

.testnet__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    @media(max-width: 991px) {
        flex: 0 0 30%;
    }
    @media(max-width: 575px) {
        flex: 0 0 40%;
        margin-left: 10px;
    }
}

.testnet__title {
    color: #999999;
    line-height: 1.2;
}

.testnet__number {
    position: relative;
    font-family: 'Bebas Neue';
    font-size: 30px;
    color: #54575a;
    font-weight: 500;
    margin-left: 20px;
    &:before {
        position: absolute;
        left: -20px;
        top: 5px;
        content: '';
        display: block;
        width: 10px;
        height: 27px;
        background-color: $yellow;
    }
}

.testnet__block {
    margin-bottom: 40px;
}

.graph__title {
    font-weight: 600;
    color: #54575a;
    font-size: 26px;
    margin-bottom: 20px;
    @media(max-width: 575px) {
        font-size: 20px;
    }
}

.tooltip {
    position: relative;
    font-weight: 300;
    display: inline-block;
    color: #999;
    border: 1px solid #999;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    font-size: 16px;
    width: 25px;
    height: 25px;
    margin-left: 5px;
    line-height: 1.5;
    text-align: center;
    &:hover {
        color: #999;
        text-decoration: underline;
    }
    &--active {
        .tooltip__block {
            opacity: 1;
            z-index: 1113;
        }
    }
}

.tooltip__block {
    position: absolute;
    top: 0;
    right: -170px;
    display: inline-block;
    background-color: #fff;
    color: #54585a;
    font-size: 14px;
    padding: 5px 10px;
    font-weight: 300;
    min-height: 50px;
    border: 1px solid #999;
    border-radius: 1px;
    z-index: -1113;
    opacity: 0;
    transition: opacity .3s;
    &:before {
        position: absolute;
        top: -1px;
        left: -20px;
        display: block;
        content: '';
        border: 10px solid transparent;
        border-top: 10px solid #999;
        border-right: 10px solid #999;
    }
    &:after  {
        position: absolute;
        top: 0;
        left: -18px;
        display: block;
        content: '';
        border: 9px solid transparent;
        border-top: 9px solid #fff;
        border-right: 9px solid #fff;
    }
    @media (max-width: 575px) {
        top: initial;
        bottom: -60px;
        right: -45px;
        &:before {
            top: -20px;
            left: 60%;
            border-top: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 10px solid #999;
            border-left: 10px solid #999;
        }
        &:after {
            top: -18px;
            left: 61%;
            border-top: 9px solid transparent;
            border-right: 9px solid transparent;
            border-bottom: 9px solid #fff;
            border-left: 9px solid #fff;
        }
    }
}

.heatmap {
    height: 224px;
}

.heatmap__pic {
    max-width: 100%;
}


