.social {

	&-list {
		justify-content: center;
	}

	&-item {
		margin: 0px 12px;

        @media(max-width: 575px) {
            margin: 0px 7px;
        }
	}
}

.social__title {
    display: inline-block;
    margin-top: 20px;
    font-size: 14px;
    color: #6e6e6e;
    transition: .3s;
}

.header-social {
    &--bottom {
        justify-content: center;
        a {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            transition: .3s;
            &:hover {
                text-decoration: none;
                color: #fff;
                .social__title {
                    color: #fff;
                }
            }
        }
        .qq-link {
            &--active {
                .qr-block {
                    opacity: 1;
                    z-index: 20;
                }
            }
            @media(min-width: 576px) {
                &:hover {
                    .qr-block {
                        opacity: 1;
                        z-index: 20;
                    }
                }
            }

        }
        .qr-block {
            position: absolute;
            top: -225px;
            left: 50%;
            transform: translate(-50%);
            opacity: 0;
            transition: .3s;
            z-index: -20;
        }
        @media (max-width: 575px) {
            li {
                margin-top: 24px;
            }
        }
    }
}