.table {
    @media(max-width: 575px) {
        display: none;
    }
}

.table__mob {
    display: none;

    @media(max-width: 575px) {
        display: block;
    }
}

.table-mob__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.table__elem {
    width: 100%;
    margin-bottom: 40px;
}

.table__row {
    border-bottom: 2px solid #f7f7f7;

    &:last-child {
        border-bottom: none;
    }
}

.table__data {
    background-color: $grey;
    color: #fff;
    padding: 10px;
    font-size: 16px;
    text-transform: uppercase;

    @media(max-width: 991px) {
        font-size: 14px;
    }

    @media(max-width: 767px) {
        font-size: 10px;
    }

    @media(max-width: 575px) {
        width: 100%;
        text-align: center;
        font-size: 14px;
    }

    span {
        text-transform: none;
    }

    &--light {
        background-color: #fff;
        color: $black;
        text-transform: none;
    }
}