.profile__form {
    padding: 0 115px;
    padding-bottom: 100px;

    @media(max-width: 991px) {
        padding: 0 50px;
        padding-bottom: 30px;
    }

    @media(max-width: 575px) {
        padding: 0 20px;
        padding-bottom: 30px;
    }
}

.form__block {
    padding-top: 90px;
}

.form__group {
    margin: 20px 0;
    padding-right: 30px;
}

.form__input {
    width: 100%;
    border: none;
    border-bottom: 2px solid #efefef;
    border-radius: 0;
    color: #999;
    font-size: 16px;
    text-transform: uppercase;
    padding: 5px 0;
    transition: .3s;

    &:hover,
    &:active {
        border-color: $orange;
        color: $black;
    }
}

.input__text {
    font-size: 12px;
    color: #999;
    margin-top: 10px;
    line-height: 1.2;
}

.form__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    height: 36px;

    .form__input {
        flex: 0 0 27%;
        height: 36px;
        display: flex;
        align-items: center;

        &--full {
            flex: 0 0 100%;
        }
    }
}

.input__title {
    text-transform: uppercase;
    color: #999;
}

.input__label {
    color: #999;
    margin-bottom: 0;
    position: relative;

    &:before {
        position: absolute;
        content: '';
        display: block;
        width: 25px;
        height: 25px;
        border: 1px solid #efefef;
        border-radius: 50%;
        top: 50%;
        left: -35px;
        transform: translateY(-50%);
    }

    &:after {
        position: absolute;
        content: '';
        display: none;
        width: 17px;
        height: 17px;
        border-radius: 50%;
        background-color: $orange;
        top: 50%;
        left: -31px;
        transform: translateY(-50%);
    }
}

.form__input--radio:checked {
    &+.input__label {
        &:after {
            display: block;
        }
    }
}

.form__text {
    color: $black;
    font-size: 14px;
    line-height: 1.2;

    &--grey {
        color: #999;
        margin-bottom: 20px;
    }
}

.file__wrapper {
    position: relative;
    margin-bottom: 30px;
}

.form__file {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.form__list {
    color: $black;
    font-size: 14px;
    list-style: none;
    padding: 0;
}

.form__list-item {
    margin-bottom: 10px;
}

.form__sublist {
    padding-left: 55px;
    padding-top: 10px;
    font-size: 14px;
    color: $black;
    list-style: none;

    li {
        margin-bottom: 10px;

        span {
            font-weight: bold;
            margin-right: 10px;
        }

        &:before {
            left: -15px;
        }
    }
}

.form__pic {
    margin: 30px 0;

    @media(max-width: 991px) {
        max-width: 100%;
        height: auto;
    }
}