.title {

	&-md {
		font-family: 'Bebas Neue';
		font-size: 24px;
	}
	&-lg {
		font-family: 'Bebas Neue';
		font-size: 36px;
	}

	&-hr {
		&:after {
	    	content: '';
	    	display: block;
	    	width: 107px;
	    	height: 5px;
	    	background: #fda700;
	    	margin-top: 15px;
	    }
	}
}
.text {
	&-md {
		font-size: 18px;
	}

	&-sm {
		font-size: 14px;
	}
	
	&-dark {
		color: #212121;
	}

	&-light {
		color: #ffffff;
	}
    &-yellow {
        color: $orange;
    }

}

*[class^='text-']{
    .mark {
    	color: #fda700;
    }
}

.br {
	display: block;
	&-sm{
		@media(min-width: 576px) {
			display: block!important;
		}
	}

	&-md {
		@media(min-width: 768px) {
			display: block!important;
		}
	}

	&-lg {
		@media(min-width: 992px) {
			display: block!important;
		}
	}

	&-none {
		display: none;
	}
}