.list {
	&-features {
		padding-left: 0;
		list-style: none;
		margin-bottom: 0;
		margin: 0 auto;
		max-width: 690px;
		.list-item {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-wrap: nowrap;
			margin-bottom: 48px;
		}
		.list-icon {
			flex: 0 0 86px;
			max-width: 86px;
			text-align: left;
			margin-right: 35px;
		}
		.list-info {}
	}
	&-dotted {
		list-style: none;
		font-size: 18px;
		line-height: 1.6;
		position: relative;
		padding-left: 0;
		li {
			margin-bottom: 28px;
			padding-left: 0;
			position: relative;
			&:before {
				content: '';
				display: block;
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background: #fda700;
				position: absolute;
				top: 9px;
				left: -55px;
				@media(max-width: 575px) {
					left: -10px
				}
			}
		}
		.mark {
			color: #fda700;
		}
	}
}