.modal-header {
    background-color: $orange;
    border-bottom: none;
    font-family: 'Bebas Neue';
    font-weight: normal;
    font-size: 48px;
    padding: 30px 0;
}

.modal-title {
    font-family: 'Bebas Neue';
    font-weight: 300;
    font-size: 48px;
    text-align: center;
    width: 100%;
    line-height: 1.1;
    padding-top: 12px;

    @media(max-width: 575px) {
        font-size: 30px;
    }
}

.modal-dialog {
    @media(min-width: 576px) {
        max-width: 772px;
    }
}

.modal-body {
    padding-top: 60px;
}

.modal-text {
    font-size: 18px;
    color: $black;
    text-align: center;

    @media(max-width: 575px) {
        font-size: 16px;
    }
}

.modal-form-elem {
    text-align: center;
}

.modal-input {
    padding: 10px 40px;
    border: 1px solid #d3d3d3;
    text-align: center;
    font-size: 18px;
    width: 295px;
    margin-top: 40px;

    @media(max-width: 575px) {
        width: initial;
        max-width: 295px;
    }
}

.modal-footer {
    display: block;
    border-top: none;
    padding-bottom: 50px;
}

.close {
    position: absolute;
    top: 20px;
    right: 20px;
    opacity: 1;
    color: #fff;
    font-size: 5.5rem;
    font-weight: 300;
}

.hidden__text {
    display: none;
    margin-top: 30px;
}