.header {
	padding-top: 122px; // padding-top: 3px;
	padding-bottom: 290px; // background: url('../img/bg-dot.png') no-repeat 50% 50px,#10171f;
	background: url('../img/bg-dot.png') 50% -120px no-repeat, url('../img/header-cloud.jpg') no-repeat 26% -9%, #10171f;
	position: relative;
	z-index: 1;

	@media(max-width: 991px) {
		padding-bottom: 25px;
		padding-top: 75px;
	}

	&:before {
		content: '';
		display: block;
		width: 187px;
		height: 195px;
		z-index: 1;
		position: absolute;
		bottom: 207px;
		right: 50%;
		transform: translateX(940px);
		background: url('../img/parts-dotted-1.png') no-repeat 50% 50% / 187px;
	}

	&-title {
		padding-top: 261px;
		font-size: 49px;
		padding-left: 30px;
		line-height: 1.2;
		letter-spacing: .25px;
		position: relative;
		left: 8px;
		margin-bottom: 72px;

		@media(max-width: 1199px) {
			padding-left: 0;
			font-size: 41px;
			padding-top: 220px;
		}

		@media(max-width: 991px) {
			padding-top: 20px;
			text-align: center;
			margin-bottom: 20px;
		}

		@media(max-width: 575px) {
			font-size: 30px;
			margin-top: -35px;
			margin-bottom: 40px;
			padding-top: 0px;
		}

		&--static {
			min-height: 380px;
		}

		&:after {
			content: '';
			display: inline-block;
			width: 2px;
			height: 42px;
			background: #fff;
			position: relative;
			top: 3px;
			left: 10px;
			animation: blick 1s linear infinite;

			@media(max-width: 991px) {
				display: none;
			}
		}

		.mark {
			color: #fda700;
		}
	}

	&-flower {
		position: relative;

		&--main {
			@media(max-width: 1199px) {
				width: 520px;
			}

			@media(max-width: 575px) {
				max-width: 490px;
			}
		}

		&--parts {
			position: absolute;
			top: 24px;
			left: -2px;

			@media(max-width: 1199px) {
				width: 480px;
			}

			@media(max-width: 575px) {
				max-width: 337px;
			}
		}
	}

	&-social {
		padding-left: 37px;

		@media(max-width: 1199px) {
			padding-left: 0;
		}

		@media(max-width: 991px) {
			justify-content: center;
		}

		@media(max-width: 575px) {
			margin: 20px 0;
		}

		li {
			margin-right: 24px;
		}

		svg,
		a {
			fill: #6e6e6e;
			transition: all .3s;

			&:hover {
				fill: #ffffff;
				color: #ffffff;
			}
		}
	}

	&-offer {
		@media(max-width: 767px) {
			line-height: 1.4;
		}

		&--elem {
			margin-top: -16px;
			position: relative;
			top: -25px;
			left: 46px;

			@media(max-width: 991px) {
				display: none;
			}
		}

		&--mobile {
			margin: 20px auto;
			margin-bottom: 60px;
		}

		@media (max-width: 575px) {
			font-size: 30px;
		}
	}
}

.section {
	position: relative;

	&-about {
		height: 587px;
		background: url('../img/bg-about.jpg') no-repeat 50% 0% / 1490px;
		z-index: 2;
		margin-top: -193px;
		padding-top: 193px;

		@media(max-width: 991px) {
			height: auto;
			margin-top: 0;
			padding-top: 0px;
		}
	}

	&-documents {
		text-align: center;
		color: #212121;
		padding-top: 82px;
		padding-bottom: 87px;
		z-index: 1;

		@media(max-width: 991px) {
			padding: 25px 0px;
		}

		&:before {
			content: '';
			display: block;
			width: 773px;
			height: 5px;
			background: #fda700;
			position: absolute;
			bottom: 0;
			right: 50%;
			transform: translateX(50%);
		}
	}

	&-key-features {
		z-index: 2;
		color: #212121;
		text-align: center;
		padding-top: 85px;
		padding-bottom: 0;
		background: url('../img/wave.png') no-repeat 50% 100% / 2560px;

		@media(max-width: 991px) {
			padding: 25px 0px 215px;
		}

        @media(min-width: 1200px) {
            height: 1800px;
        }

		&:before {
			content: '';
			display: block;
			width: 534px;
			height: 552px;
			z-index: 1;
			position: absolute;
			top: -295px;
			right: 50%;
			transform: translateX(-746px);
			background: url('../img/parts-dotted-2.png') no-repeat 50% 50% / 534px;
		}

		&:after {
			content: '';
			display: block;
			width: 233px;
			height: 194px;
			z-index: 1;
			position: absolute;
			top: 254px;
			right: 50%;
			transform: translateX(-516px);
			background: url('../img/parts-dotted-3.png') no-repeat 50% 50% / 233px;
		}
	}

	&-ecosystem {
		background: #041019;
		color: #ffffff;
		text-align: center;
		padding-top: 104px;
		padding-bottom: 330px;
		z-index: 1;

		@media(max-width: 991px) {
			padding-top: 10px;
			padding-bottom: 35px;
		}
        @media(min-width: 1200px) {
            padding-top: 170px;
        }
	}

	&-anatomy {
		padding-top: 204px;
		padding-bottom: 491px;
		color: #212121;

		@media(max-width: 575px) {
			padding-top: 144px;
			padding-bottom: 411px;
		}
	}

	&-shardforks {
		background: #10171f;
		color: #212121;
		position: relative;
		z-index: 1;

		.container {
			position: relative;
			top: -368px;
		}
	}

	&-technical {
		color: #212121;
		background: #ffffff;
		position: relative;
		z-index: 2;
		text-align: center;
		margin-top: -249px;
		padding-top: 134px;
		padding-bottom: 120px;

		@media(max-width: 575px) {
			padding-top: 50px;
			padding-bottom: 50px;
		}
	}

	&-testnet {
		color: $black;
        padding-top: 100px;
        .title-lg span {
            color: $yellow;
        }
        @media(max-width: 575px) {
            padding-top: 50px;
        }
	}
    &-team-inner {
        color: $black;
        padding: 200px 0;
    }

    &-graphs {
        background-color: #f9f8f6;
        padding-top: 60px;
    }
    &-tables {
        background-color: #f9f8f6;
        padding-bottom: 60px;
        &--inner {
            padding-top: 60px;
        }
    }


	&-algorithm {
		background: #10171f;
		text-align: center;
		padding: 100px 0px 126px;
	}

	&-protection {
		background: #ffffff;
		text-align: center;
		padding: 154px 0px 249px;
		color: #212121;

		@media(max-width: 575px) {
			padding: 50px 0 50px;
		}
	}

	&-foundation {
		background: #10171f;
		color: #ffffff;
		padding-top: 177px;
		padding-bottom: 94px;

		.list-dotted {
			margin-left: 50px;
			margin-top: 66px;

			@media(max-width: 575px) {
				margin-left: 15px;
			}

			li {
				&:before {
					@media(max-width: 575px) {
						left: -10px
					}
				}
			}
		}

		.flower-foundation {
			position: relative;
			top: 35px;
			left: -4px;
		}
	}

	&-mission {
		background: url('../img/bg-mission.jpg') no-repeat 50% 0% / 1522px, #f5f5f5;
		position: relative;
		padding-top: 193px;
		padding-bottom: 120px;

		@media (max-width: 767px) {
			padding: 100px 0;
		}

		@media(max-width: 575px) {
			padding: 75px 0;
		}

		.list-wrapper {
			width: 662px;
			margin: 0 auto;

			@media(max-width: 991px) {
				width: 500px;
			}

			@media(max-width: 575px) {
				width: 300px
			}
		}

		.list-title {
			text-transform: uppercase;
			color: #fda700;
			font-weight: bold;
			font-size: 18px;
			width: 770px;
			margin: 0 auto;
			margin-bottom: 30px;

			@media(max-width: 991px) {
				width: 500px;
				margin-top: 30px;
			}

			@media(max-width: 575px) {
				width: 300px
			}
		}

		&:after {
			position: absolute;
			display: block;
			content: '';
			width: 770px;
			height: 5px;
			background-color: #fda700;
			left: 50%;
			bottom: -5px;
			transform: translate(-50%);
			z-index: 1;

			@media(max-width: 991px) {
				width: 500px;
			}

			@media(max-width: 575px) {
				width: 300px
			}
		}
	}

	&-team {
		background: url(../img/bg-team.png) no-repeat 55% 130%;
		background-size: 15%;
		color: #212121;
		padding-top: 100px;
		padding-bottom: 300px;

		@media(max-width: 1920px) {
			background-position: 60% 135%;
			background-size: 20%;
		}

		@media(max-width: 991px) {
			padding: 100px 0;
			background: #fff;
		}

		@media(max-width: 575px) {
			padding: 75px 0;
		}
	}

	&-footer {
		z-index: 2;
		color: #212121;
		padding-bottom: 0px;

		//@media(max-width: 991px) {
		//	padding-bottom: 35px;
		//}

		&:before {
			content: '';
			display: block;
			width: 534px;
			height: 552px;
			z-index: 1;
			position: absolute;
			top: -250px;
			right: 50%;
			transform: translateX(-746px);
			background: url('../img/parts-dotted-2.png') no-repeat 50% 50% / 534px;
		}

		&:after {
			content: '';
			display: block;
			width: 233px;
			height: 194px;
			z-index: 1;
			position: absolute;
			top: 335px;
			right: 50%;
			transform: translateX(630px);
			background: url('../img/parts-dotted-4.png') no-repeat 50% 50% / 233px;
		}

		.footer-line {
			position: absolute;
			top: 65px;
			width: 100%;
			height: 195px;
			background-color: #10171f;
		}

        .footer__bottom {
            width: 100%;
            padding: 80px 0;
            background-color: #10171f;
            margin-top: 120px;
            @media (max-width: 575px) {
                padding-top: 56px;
            }
        }
        
        .footer__copyright {
            width: 100%;
            text-align: center;
            background-color: #1d232a;
            color: #6e6e6e;
            font-size: 14px;
            padding: 15px 0;
        }

		.title-hr {
			&:after {
				margin-top: 57px;

				@media(max-width: 991px) {
					margin-top: 15px;
				}
			}
		}

		.about-panel--right {
			&:before {
				display: none;
			}
		}

		.form-control {
			padding: 25px 15px;
		}
	}
}

.shardforks-text {
	position: relative;
	z-index: 1;
}

.shardforks-image {
	position: relative;
	margin-top: 174px;
	z-index: 0;
	text-align: center;

	&:before {
		content: '';
		display: block;
		width: 531px;
		height: 535px;
		position: absolute;
		z-index: 1;
		top: -50px;
		left: 50%;
		margin-left: -245px;
	}

	&--dark {
		&:before {
			background: url('../img/circle-graf.png') no-repeat 50% 50% / 531px;
		}
	}

	&--light {
		&:before {
			background: url('../img/circle-graf-light.png') no-repeat 50% 50% / 531px;
		}
	}

	img {
		position: relative;
		z-index: 1;
		width: 168px;
	}
}

.advantages__slide {
	width: 100%;
	padding: 10px 0;

	@media(max-width: 575px) {
		width: 90%;
		padding: 14px 0;
	}
}

.advantages__text {
	font-size: 18px;
	color: $black;

	@media(max-width: 575px) {
		font-size: 16px;
	}
    &--lines {
        margin: 40px 0;
        position: relative;
        &:after {
            position: absolute;
            left: 50%;
            bottom: -20px;
            transform: translate(-50%);
            content: '';
            display: block;
            width: 60%;
            height: 2px;
            background-color: #ffca59;
        }
        &:before{
            position: absolute;
            left: 50%;
            top: -20px;
            transform: translate(-50%);
            content: '';
            display: block;
            width: 60%;
            height: 2px;
            background-color: #ffca59;
        }
    }
}

.advantages__items {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: url(../img/adv-bg.png) no-repeat 30% 50%;
	background-size: 70%;
	margin: 20px 0;

	@media(max-width: 991px) {
		background: url(../img/adv-bg.png) no-repeat 50% 50%;
		background-size: 65%;
	}

	@media(max-width: 767px) {
		background: none;
	}
}

.advantage__item {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 125px;
	height: 125px;
	border: 2px solid $yellow;
	border-radius: 50%;
	background-color: #fff;

	@media(max-width: 575px) {
		width: 85px;
		height: 85px;
	}
}

.advantage__title {
	color: $black;
	font-weight: bold;
	line-height: 1.2;

	@media(max-width: 575px) {
		font-size: 14px;
	}

	&--big {
		color: #fff;
		font-size: 60px;
		font-weight: normal;
		margin: 10px 0;
		line-height: 1;

		@media(max-width: 575px) {
			font-size: 30px;
		}
	}

	&--yellow {
		color: $yellow;
		font-size: 60px;
		font-weight: normal;
		margin: 20px 0;
		line-height: 1;

		@media(max-width: 575px) {
			font-size: 30px;
		}
	}
}

.advantage__block {
	position: relative;
	background-color: $yellow;
	padding-top: 35px;
	padding-bottom: 147px;
	width: 100%;
	overflow: hidden;

	@media(max-width: 575px) {
		width: 90%;
		padding: 35px 5px;
		padding-bottom: 114px;
	}

	@media(max-width: 350px) {
		max-width: 250px;
	}

	&--light {
		background-color: #fff;
		padding-bottom: 42px;
		border: 2px solid #e7e7e7;
	}
    &--last {
        padding-bottom: 35px;
    }
}

.advantage__pic {
	position: absolute;
	bottom: -70px;
	left: -90px;
}

.adv__button {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 10;

	&--prev {
		left: 0px;
	}

	&--next {
		right: 0px;
	}
}

.slider__icon {
	color: rgba(0, 0, 0, .6);

	&:hover {
		color: $yellow;
	}
}

.adv__pagination {
	left: 50%;
	transform: translate(-50%);
	bottom: -60px;

	span {
		margin: 0 3px;
	}
}