.navbar {
	padding: 1.5rem 0rem;
	background: #10171f;
	transition: all .3s;

	@media (max-width: 1199px) {
		padding: .5rem 0rem;
	}

	&-collapse {
		// padding-left: 120px;
		flex-grow: 0;
	}

	// &-drop {
	// 	background: #0a2334;
	// }
	&-nav {}

	&-drop {
		.nav-item {
			text-align: center;
			padding-bottom: 20px;
		}
	}

	&-dark {
		.navbar-nav {
			.nav-link {
				color: #ffffff;
				font-size: 17px;
				padding: 0 20px;

				&:hover {
					color: #fda700;
				}
			}
		}
	}

	&-toggler {
		border: none;
	}

	&-brand {
		margin-right: 83px;
		transition: all .3s;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;

		@media(max-width: 1199px) {
			margin-right: 0px;
		}

		@media (max-width: 575px) {
			margin-left: 15px;
		}

		img {
			height: 60px;
			transition: all .3s;
		}
	}

	.btn-orange--inverse {
		@media (max-width: 575px) {
			margin-right: 15px;
		}
	}

	.navbar-logo--full {
		// display: none;
		height: 60px;
		z-index: 1;
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
	}

	.navbar-logo--icon {
		// display: block;
		height: 60px;
		position: relative;
		z-index: 2;
	}

	&.scrolled {
		padding: 7px 0px !important;

		.navbar-brand {
			padding-top: 0;
			padding-bottom: 0;
		}

		.navbar-logo--full {
			// display: none;
			height: 0px !important;
		}

		.navbar-logo--icon {
			// display: block;
			height: 41px !important;
		}
	}
}