.kyc {
    padding: 25px 70px;

    @media(max-width: 767px) {
        padding: 25px 20px;
    }
}

.kyc__steps {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: nowrap;

    @media(max-width: 991px) {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.kyc__step {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $grey;
    text-align: center;
    font-size: 16px;
    flex: 0 0 20%;

    @media(max-width: 991px) {
        flex: 0 0 50%;
        margin: 20px 0;
    }

    @media(max-width: 575px) {
        flex: 0 0 100%;
    }

    &:last-child {
        .kyc__dot {
            &:after {
                display: none;
            }
        }
    }

    &--active {
        color: $black;

        .kyc__dot {
            display: none;

            &--active {
                display: flex;
            }
        }
    }
}

.kyc__title {
    line-height: 1.1;
}

.kyc__dot {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    background-color: $grey;
    border-radius: 50%;
    color: #fff;
    margin: 10px 0;

    &:after {
        position: absolute;
        display: block;
        content: '';
        width: 135px;
        height: 2px;
        background-color: $grey;
        top: 50%;
        left: 30px;
        transform: translateY(-50%);

        @media(max-width: 991px) {
            display: none;
        }
    }

    &--active {
        display: none;
        background-color: $orange;
    }
}

.kyc__text {
    text-align: center;
    font-size: 12px;
    line-height: 1.1;
}

.kyc__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $black;
    padding: 10px 30px;
    border: 1px solid #efefef;
    width: 90%;
    margin: 0 auto;
    margin-top: 30px;

    @media(max-width: 991px) {
        width: 100%;
    }

    @media(max-width: 575px) {
        flex-direction: column;
    }

    .info__sub {
        font-size: 12px;

        @media(max-width: 575px) {
            text-align: center;
            margin-top: 10px;
        }
    }
}