.testnet__table {
    width: 100%;
}

.tn-table__elem {
    table-layout: fixed;
    width: 100%;
    color: $black;
    tr,
    th,
    td {
        padding: 10px 20px;
        border: 1px solid #e3e3e3;
        @media(max-width: 767px){
            padding: 10px;
        }
    }
    th {
        background-color: #f1f1f1;
        color: #54575a;
        @media(max-width: 575px) {
            overflow-x: hidden;
            text-overflow: ellipsis;
        }
    }
    td {
        background-color: #fff;
        text-align: center;
        overflow-x: hidden;
        text-overflow: ellipsis;
    }

    .td-last {
        width: 8%;
        @media(max-width: 991px) {
            width: 13%;
        }
        @media(max-width: 575px) {
            width: 20%;
        }
    }

    &--first {
        .td-first {
            width: 12%;
            @media(max-width: 991px) {
                width: 20%;
            }
            @media(max-width: 767px) {
                width: 15%;
            }
            @media(max-width: 430px) {
                width: 22%;
            }
        }

        .td-last {
            width: 20%;
            @media(max-width: 1999px) {
                width: 25%;
            }
            @media(max-width: 991px) {
                width: 30%;
                white-space: nowrap;
            }
            @media(max-width: 430px) {
                width: 25%;
            }
        }
    }
}

.table__icon {
    margin-left: 10px;
    @media(max-width: 767px) {
        display: none;
    }
}

