.btn {
	text-align: center;

	&-small {
		font-size: 14px;
		padding: 6px 25px;
	}

	&-default {
		border-width: 2px;
		font-size: 18px;
		padding: 15px 45px;
	}

	&-large {}

	&-orange {
		background: #fda700;
		color: #ffffff;
		border-color: #fda700;

		&:hover {
			background: transparent;
			color: #212121;
			border-color: #212121;
		}

		&--inverse {
			background: transparent;
			color: #fda700;
			border-color: #fda700;

			&:hover {
				background: #fda700;
				color: #ffffff;
			}
		}
	}

	&-transparent {
		&--white {
			border-color: #ffffff;
			color: #ffffff;
			background: transparent;

			&:hover {
				background: #fda700;
				border-color: #fda700;
			}
		}

		&--dark {
			border-color: #212121;
			color: #212121;
			background: transparent;

			&:hover {
				background: #fda700;
				color: #ffffff;
				border-color: #fda700;
			}
		}
	}

	&-dark {
		border-color: #0a192c;
		color: #0a192c;
		background: transparent;
	}

	&-box {
		border-radius: 3px;
	}

	&-round {
		border-radius: 3rem;
	}

	&-fluid {
		width: 100%;
	}

	&-signup {
		margin-bottom: 321px;

		@media(max-width: 767px) {
			margin-bottom: 160px;
		}
	}

	&-submit {
		padding: 6px 40px;
	}

	&-disabled {
		background-color: $grey;
		user-select: none;
		border-color: $grey;

		&:hover {
			background-color: $grey;
			border-color: $grey;
			color: #fff;
		}
	}
    &-testnet {
        background-color: #f1f1f1;
        color: #54585a;
        font-size: 16px;
        border: 1px solid #e3e3e3;
        padding: 5px 20px;
    }
}